body {
    font-family: 'Comic Sans MS', sans-serif;
    background-color: #f2f2f2; /* Light grey */
    margin: 0;
    padding: 0;
  }
  
  /* Comic book style dots (halftone effect) */
  .home-container {
    background-image: radial-gradient(circle, rgba(255, 215, 0, 0.5) 20%, transparent 20%), radial-gradient(circle, rgba(255, 215, 0, 0.5) 20%, transparent 20%);
    background-size: 50px 50px;
    background-position: 0 0, 25px 25px;
    padding: 40px;
    text-align: center;
  }
  
  /* Main Content */
  .main-content {
    margin-top: 50px;
    padding: 20px;
    background-color: #fff;
    border: 5px solid black;
    display: inline-block;
    box-shadow: 10px 10px 0 #ffdd00, 10px 10px 0 0 black;
  }
  
  .main-title {
    font-size: 3rem;
    color: #ff0000;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-shadow: 3px 3px #000;
  }
  
  .description {
    font-size: 1.5rem;
    line-height: 1.6;
    color: #333;
    margin-top: 20px;
    text-align: center;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    font-style: italic;
    border: 3px solid black;
    padding: 20px;
    background-color: #ffe600;
    box-shadow: 5px 5px 0 #000;
  }
  
  /* Halftone dots effect */
  .halftone {
    background: radial-gradient(circle, rgba(0, 0, 0, 0.1) 1px, transparent 1px), radial-gradient(circle, rgba(0, 0, 0, 0.1) 1px, transparent 1px);
    background-size: 30px 30px;
  }  